.image-card {
  width: 20rem;
  position: relative;
  padding: 2rem;
}

.image-container {
  height: 25rem;
  border-radius: 1rem;
  overflow: hidden;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.image-container img {
  width: 20rem;
  height: 25rem;
  transition: transform 200ms;
}

.image-container img:hover {
  transform: scale(1.1);
  filter: sepia(0.5);
}

.image-info {
  color: rgb(255, 255, 255);
  font-size: 1rem;
  font-weight: 700;
  position: absolute;
  top: 80%;
  text-align: right;
  padding-right: 1rem;
  background-color: rgba(0, 0, 0, 0.534);
  width: max-content;
  border-top-right-radius: 1rem;
  border-bottom-right-radius: 1rem;
}

.image-info p {
  padding-left: 2rem;
}
